@font-face {
  font-family: "SF Pro Display";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Display"), url("./fonts/SF-Pro-Display-Regular.woff2") format("woff2"), url("./fonts/SF-Pro-Display-Regular.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Display";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Display"), url("./fonts/SF-Pro-Display-Medium.woff2") format("woff2"), url("./fonts/SF-Pro-Display-Medium.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Display";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Display"), url("./fonts/SF-Pro-Display-Semibold.woff2") format("woff2"), url("./fonts/SF-Pro-Display-Semibold.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Text";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Text"), url("./fonts/SFProText-Regular.woff2") format("woff2"), url("./fonts/SFProText-Regular.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Text";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Text"), url("./fonts/SFProText-Medium.woff2") format("woff2"), url("./fonts/SFProText-Medium.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Text";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Text"), url("./fonts/SFProText-Semibold.woff2") format("woff2"), url("./fonts/SFProText-Semibold.woff") format("woff");
}
*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}

.container {
  position: relative;
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 890px;
  max-width: 1440px;
  width: 100%;
  font-family: "Inter", sans-serif;
  background-color: #F5F9FF;
  font-size: 1rem;
  line-height: 1.375rem;
  font-weight: 400;
  color: #5F6B77;
}

.container::after {
  content: "";
  z-index: -10;
  position: absolute;
  width: 458px;
  height: 100%;
  top: 0;
  right: 0;
  background-color: #ECF2FE;
  border-radius: 0 20px 20px 0;
  background: linear-gradient(45deg, #506CCF 0%, #3453C2 100%);
}

.page-main {
  padding: 0 20px;
  padding-top: 9.2rem;
  overflow: hidden;
  padding-right: 2.5rem;
  padding-left: 8.375rem;
}

.button {
  font-family: "SF Pro Display", sans-serif;
  font-size: 1rem;
  line-height: 1.375rem;
  font-weight: 500;
  color: white;
  padding: 0.75rem 1.5625rem 0.75rem 1.5rem;
  max-width: max-content;
  width: 127px;
  height: 46px;
  background-color: #49A1F1;
  border-radius: 10px;
  cursor: pointer;
}
.button--purple {
  background-color: #506CCF;
}
.button--transparent {
  background-color: transparent;
  border: 1px solid #E9EAF0;
}
.button:hover {
  opacity: 0.8;
}

.heading {
  font-family: "SF Pro Display", sans-serif;
  font-size: 3rem;
  line-height: 3.625rem;
  font-weight: 600;
  color: #08123A;
  max-width: 100%;
  width: max-content;
  padding: 0;
  margin: 0;
}

.description {
  text-align: left;
}

.page-header {
  padding: 1.75rem 2.5rem 0rem 8.375rem;
  max-width: 100%;
  width: 100%;
}

.main-nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: auto;
  gap: 60px;
}
.main-nav__logo img {
  width: 36.82px;
  height: 36.97px;
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__wrapper {
  position: absolute;
  border-radius: 14px;
  padding: 20px 40px;
  right: 0px;
  width: 100%;
}
.main-nav__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  column-gap: 35px;
  row-gap: 20px;
}
.main-nav__contacts {
  display: flex;
  align-items: center;
}
.main-nav__contacts #language {
  font-family: "SF Pro Display", sans-serif;
  cursor: pointer;
  border: none;
  background-color: transparent;
  color: #8B9FCC;
  appearance: none;
  padding-right: 76px;
}
.main-nav__contacts a {
  display: block;
  text-decoration: none;
  color: white;
  min-width: max-content;
  padding-right: 15px;
}

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  border-radius: 15px;
  background-color: #f5f9ff;
}
.site-list__item a {
  text-decoration: none;
  color: inherit;
  font-family: "SF Pro Text", sans-serif;
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: 500;
  color: #5F6B77;
}
.site-list__item a:hover {
  opacity: 0.6;
}

.promo {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.promo__wrapper {
  position: relative;
  width: fit-content;
  width: 50%;
  min-width: 358px;
  max-width: 620px;
  display: flex;
  flex-direction: column;
  gap: 41px;
  padding: 0.8rem 0.8rem 0.8rem 0;
  border-radius: 15px;
  background-color: #f5f9ff;
}
.promo__heading {
  font-size: 3rem;
  line-height: auto;
  font-weight: black;
  color: 500;
}
.promo__description {
  font-size: 20px;
  line-height: 28px;
  font-weight: black;
  color: 400;
  align-self: center;
  padding-right: 30px;
}
.promo__button {
  font-size: 1rem;
  line-height: 1.375rem;
  font-weight: 500;
  color: white;
  width: 170px;
  height: 54px;
}
.promo__button-down {
  width: 54px;
  height: 54px;
  position: relative;
}
.promo__button-down svg {
  position: absolute;
  top: 19px;
  right: 17px;
}
.promo__image {
  margin-top: -100px;
  margin-right: -72px;
  width: 928px;
  height: 669px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../../img/diagrams.svg");
}

.social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.social-list a:hover {
  opacity: 0.6;
}